'use client';

import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';

// Схема валидации для авторизации
const loginSchema = z.object({
	email: z.string().email('Некорректный email'),
	password: z.string().min(1, 'Пароль обязателен'),
});

type LoginFormData = z.infer<typeof loginSchema>;

const LoginForm = () => {
	const {
		register,
		handleSubmit,
		formState: { errors, isSubmitting },
		reset,
	} = useForm<LoginFormData>({
		resolver: zodResolver(loginSchema),
	});

	const onSubmit = async (data: LoginFormData) => {
		try {
			console.log('Данные авторизации:', data);
			// Здесь ваш API запрос
			// await loginUser(data);
			reset();
			alert('Авторизация успешна!');
		} catch (error) {
			console.error('Ошибка авторизации:', error);
		}
	};

	return (
		<div className='max-w-md mx-auto p-6 bg-white rounded-lg shadow-lg'>
			<h2 className='text-2xl font-bold mb-6 text-center'>Авторизация</h2>

			<form onSubmit={handleSubmit(onSubmit)} className='space-y-4'>
				{/* Email */}
				<div>
					<label className='block text-sm font-medium text-gray-700 mb-1'>Email *</label>
					<input
						{...register('email')}
						type='email'
						className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500'
						placeholder='example@mail.com'
					/>
					{errors.email && <p className='text-red-500 text-sm mt-1'>{errors.email.message}</p>}
				</div>

				{/* Пароль */}
				<div>
					<label className='block text-sm font-medium text-gray-700 mb-1'>Пароль *</label>
					<input
						{...register('password')}
						type='password'
						className='w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500'
						placeholder='Введите пароль'
					/>
					{errors.password && <p className='text-red-500 text-sm mt-1'>{errors.password.message}</p>}
				</div>

				<button
					type='submit'
					disabled={isSubmitting}
					className='w-full bg-green-500 text-white py-2 px-4 rounded-md hover:bg-green-600 focus:outline-none focus:ring-2 focus:ring-green-500 disabled:opacity-50 disabled:cursor-not-allowed'
				>
					{isSubmitting ? 'Вход...' : 'Войти'}
				</button>
			</form>
		</div>
	);
};

export default LoginForm;
